export default {
	data() {
		return {
			key: '',
			title: '',
			manual:'<p style="color: red">测试数据</p>'
		};
	},
	onLoad(options) {
		this.key = options.key
		this.title = options.title
		uni.setNavigationBarTitle({
		　　title: this.title
		})
		// this.getManual()
	},
	methods: {
		getManual(){
			this.$api.personalContact({key: this.key}).then(res => {
				console.log(res)
				if(res.data.errno === 0){
					this.manual = res.data.data
				}
			})
		}
	}
}
